import React from "react";
import styled from "styled-components";

const ParralaxContainer = styled.div`
  min-height: 10px;
  padding: 30vw 0 5vw;
  position: relative;
  transform-style: inherit;
  width: 100vw;
  background: 50% 50% / cover;
  &::after {
    content: "Back Layer";
    display: block;
    background: 50% 50% / cover;
    background-size: cover;
    transform-origin: center center 0;
    transform: translate(-1px) scale(2);
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    position: absolute;
    background-color: lightblue;
    background: url(https://images.pexels.com/photos/754082/pexels-photo-754082.jpeg);
    z-index: -1;
    min-height: 10px;
  }
`;

const ParallaxBaseLayer = styled.div`
  background: dodgerblue;
  color: white;
  magrin-top: -100px;
`;

const Parallax = ({ children }) => (
  <ParralaxContainer>
    <ParallaxBaseLayer>{children}</ParallaxBaseLayer>
  </ParralaxContainer>
);
export default Parallax;



